body,
html {
  margin: 0;
  padding: 0;
}
* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.quiz-container {
  height:100vh;
  display:flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to bottom, #A6ACAF, #E5E7E9 );
}

.quiz-container__question {
  border-radius: 0.5rem;
  padding: 40px 30px 40px 30px;
  height: 500px;
  width: 70%;
  margin: 0 auto;
  text-align: center;
  background-color: #0BCECB;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 30px;
  box-shadow: 20px 20px 20px 0px rgba(0, 0, 0, 0.7);
  box-sizing: border-box;
}

.quiz-container__options {
  padding: 2rem;
  border-top: 1px solid #fff;
}

.quiz-container__ul {
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 1rem;
  list-style-type: none;
}

.quiz-container__li {
  padding: 1rem;
  background-color: #fff;
  color: #000;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: all 0.5s ease;
  box-shadow: 10px 15px 15px #000;
  font-size: 25px;
}

.quiz-container__li:hover {
  box-shadow: 0px 1px 10px #fff;
  background-color:aqua;
}
.btn {
  background: #0BCECB;
  color: #000;
  font-size: 20px;
  margin: 15px 0;
  border: 2px solid #000;
  padding: 10px 15px;
  text-transform: uppercase;
  transition: 0.5 all ease;
  opacity: 1;
  margin-left: 15%;
}

.btn:hover{
  opacity: 0.7;
}
@media (max-width: 941px) {
  .quiz-container__li {
    font-size: 20px;
  }
  
  .quiz-container__question {
    border-radius: 0.5rem;
    padding-top: 10px 25px 10px 25px;
    height: 450px;
    width: 600px;
    font-size: 25px;
  }
}

@media (max-width: 690px) {
  .quiz-container__li {
    padding: 0.5rem;
    border-radius: 0.5rem;
    font-size: 20px;
    box-sizing: border-box;
  }
  
  .quiz-container__ul {
    grid-template-columns: 350px;
    grid-template-rows:  50px 50px 50px 50px;
    grid-gap: 2rem;
    list-style-type: none;
  }
  .quiz-container__question {
    border-radius: 0.5rem;
    padding: 30px 15px 10px 15px;
    height: 600px;
    width: 430px;
    font-size: 25px;
    box-sizing: border-box;
  }
  .quiz-container__options {
    padding: 1rem;
  }
}
@media (max-width: 500px) {
  .quiz-container__li {
    padding: 1rem;
    border-radius: 0.5rem;
    font-size: 17px;
    box-sizing: border-box;
  }
  
  .quiz-container__ul {
    padding: 0;
    display: grid;
    grid-template-columns: 230px;
    grid-template-rows:  50px 50px 50px 50px;
    grid-gap: 1rem;
    list-style-type: none;
  }
  .quiz-container__question {
    border-radius: 0.5rem;
    padding: 30px 15px 10px 15px;
    height: 550px;
    width: 300px;
    font-size: 1.5rem;
    box-sizing: border-box;
  }
  .quiz-container__options {
    padding: 1rem;
  }
}